$left-container-width: 24%;
$middle-container-width: 50%;
$container-border: 2px solid #ccc;
.analysis-container {
  // display: flex;
  .left-container {
    // flex-shrink: 0;
    float: left;
    height: 100%;
    width: $left-container-width;
    border-right: $container-border;
  }
  .middle-container {
    // flex-shrink: 0;
    float: left;
    height: 100%;
    width: $middle-container-width;
    border-right: $container-border;
    display: flex;
    flex-direction: column;
    .middle-top-container {
      width: 100%;
      height: 66%;
      border-bottom: $container-border;
    }
    .middle-bottom-container {
      width: 100%;
      flex: 1;
    }
  }
  .right-container {
    // flex: 1;
    // float: left;
    margin-left: $left-container-width + $middle-container-width;
    height: 100%;
    flex-direction: column;
    .right-top-container {
      width: 100%;
      height: 40%;
      border-bottom: $container-border;
    }
    .right-middle-container {
      width: 100%;
      height: 33%;
      border-bottom: $container-border;
    }
    .right-bottom-container {
      width: 100%;
      height: 33%;
    }
    .right-middle-bottom-container {
      position: relative;
      width: 100%;
      height: 60%;
    }
  }
  // .hover-tooltip-container {
  //   position: fixed;
  //   top: 0;
  //   left: 0;
  // }
}

